export default function GsdtlistCtrl() {
  var vm = this;
  vm.list = [
    { name: '', key: 'frontpic', width: '20%', img: true },
    { name: '', key: 'title', width: '20%' },
    { name: '', key: 'visitors', width: '20%' },
    { name: '', key: 'state', width: '10%' },
    { name: '', key: 'type', width: '20%' },
  ];
}



